export default "import { confirm } from '../index';\nimport React from 'react';\nimport Button from 'components/Button';\n\nconst DialogExample4: React.FC = () => {\n  return (\n    <div style={{ marginBottom: '20px' }}>\n      <h1>Example 4</h1>\n      <Button\n        onClick={() =>\n          confirm(\n            'Confirm',\n            'hhh',\n            () => console.log('ok'),\n            () => console.log('cancel')\n          )\n        }\n      >\n        confirm\n      </Button>\n    </div>\n  );\n};\nexport default DialogExample4;\n";