export default "import * as React from 'react';\nimport Form, { FieldsType, FormDataType } from '../index';\nimport Button from '../../Button';\nimport { useState } from 'react';\nimport Validator, { FormRules, FormErrorTYpe } from '../validator';\n\ninterface Props extends React.HTMLAttributes<HTMLElement> {}\n\nconst usernames = ['bob', 'jack', 'alice', 'Lucy'];\nconst checkUserName = (\n  username: string,\n  succeed: () => void,\n  fail: () => void\n) => {\n  setTimeout(() => {\n    if (usernames.indexOf(username) >= 0) {\n      succeed();\n    } else {\n      fail();\n    }\n  }, 1000);\n};\nconst FormExample1: React.FC<Props> = (props) => {\n  const [formData, setFormValue] = useState<FormDataType>({\n    username: 'aaa',\n    password: '123456',\n  });\n  const [fields] = useState<Array<FieldsType>>([\n    {\n      name: 'username',\n      label: '用户名',\n      input: { type: 'text' },\n    },\n    {\n      name: 'password',\n      label: '密码',\n      input: { type: 'password' },\n    },\n  ]);\n  const [errors, setErrors] = useState<FormErrorTYpe>({});\n  const onSubmit = () => {\n    const rules: FormRules = [\n      {\n        key: 'username',\n        required: true,\n        minLens: 4,\n        maxLens: 6,\n        pattern: /^[a-zA-Z]+$/,\n      },\n      {\n        key: 'username',\n        validator: {\n          name: 'unique',\n          validate(username: string) {\n            return new Promise((resolve, reject) => {\n              checkUserName(username, resolve, reject);\n            });\n          },\n        },\n      },\n      {\n        key: 'password',\n        required: true,\n      },\n    ];\n    Validator(formData, rules, (errors) => {\n      setErrors(errors);\n    });\n  };\n  return (\n    <div style={{ marginBottom: '20px' }}>\n      <Form\n        value={formData}\n        fields={fields}\n        buttons={\n          <>\n            <Button type=\"primary\" style={{ marginRight: '12px' }}>\n              提交\n            </Button>\n            <Button>取消</Button>\n          </>\n        }\n        onSubmit={onSubmit}\n        onChange={(newValue) => {\n          setFormValue(newValue);\n        }}\n        errors={errors}\n        errorDisplayMode={'all'}\n      />\n    </div>\n  );\n};\nexport default FormExample1;\n";